.alert-success {
  position: relative;
  z-index: -1;
  top: -30px;
  margin-bottom: -30px;
  left: 0;
  right: 0;
  text-align: center;
  overflow: hidden;
  height: 30px;
  line-height: 0;
  border-radius: 0;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
  -webkit-animation: slideDown 2.5s 0.2s 1 ease forwards;
          animation: slideDown 2.5s 0.2s 1 ease forwards;
}

@-webkit-keyframes slideDown {
  0%, 100% { -webkit-transform: translateY(-30px); transform: translateY(-30px); }
  10%, 90% { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

@keyframes slideDown {
